import { useState, useEffect, useMemo } from 'react';

//   API  
const createDummyElectronAPI = (): ElectronAPI => ({
  onTypingStatsUpdate: () => () => {},
  onStatsSaved: () => () => {},
  startTracking: () => console.log(' startTracking '),
  stopTracking: () => console.log(' stopTracking '),
  saveStats: () => console.log(' saveStats '),
  getCurrentBrowserInfo: () => Promise.resolve({ name: null, isGoogleDocs: false, title: null }),
  getDebugInfo: () => Promise.resolve({
    isTracking: false,
    currentStats: {
      keyCount: 0,
      typingTime: 0,
      startTime: null,
      lastActiveTime: null,
      currentWindow: null,
      currentBrowser: null,
      totalChars: 0,
      totalWords: 0,
      totalCharsNoSpace: 0,
      pages: 0,
      accuracy: 100
    },
    platform: 'web',
    electronVersion: 'N/A',
    nodeVersion: 'N/A'
  }),
  saveSettings: (settings: SettingsState) => Promise.resolve({ success: true, settings }),
  loadSettings: () => Promise.resolve({
    enabledCategories: { docs: true, office: true, coding: true, sns: true },
    autoStartMonitoring: true,
    darkMode: false,
    windowMode: 'windowed',
    minimizeToTray: true,
    showTrayNotifications: true,
    reduceMemoryInBackground: true,
    enableMiniView: true,
    //    
    useHardwareAcceleration: false,
    processingMode: 'auto',
    maxMemoryThreshold: 100,
    // resumeAfterIdle  
    resumeAfterIdle: true
  }),
  setDarkMode: () => Promise.resolve({ success: true }),
  setWindowMode: () => Promise.resolve({ success: true }),
  getWindowMode: () => Promise.resolve('windowed' as WindowModeType),
  windowControl: () => {},
  checkAutoStart: () => {},
  onAutoTrackingStarted: () => () => {},
  onSwitchTab: () => () => {},
  onOpenSaveStatsDialog: () => () => {},
  requestStatsUpdate: () => {},
  onMiniViewStatsUpdate: () => () => {},
  toggleMiniView: () => {},
  updateTraySettings: () => Promise.resolve({ success: true }),
  quitApp: () => console.log(' quitApp '),
  toggleWindow: () => console.log(' toggleWindow '),
  onBackgroundModeChange: () => () => {},
  onTrayCommand: () => () => {},
  restartApp: () => console.log(' restartApp '),
  // showRestartPrompt  
  showRestartPrompt: () => console.log(' showRestartPrompt '),
  // closeWindow  
  closeWindow: () => console.log(' closeWindow '),
  // getDarkMode  
  getDarkMode: () => Promise.resolve(false)
});

export function useElectronApi() {
  const [electronAPI, setElectronAPI] = useState<ElectronAPI | null>(null);

  //  API -    API   
  const dummyApi = useMemo(() => createDummyElectronAPI(), []);

  //    API -    API 
  const api = useMemo(() => electronAPI || dummyApi, [electronAPI, dummyApi]);

  // window  electronAPI  (  )
  useEffect(() => {
    if (typeof window !== 'undefined') {
      if (window.electronAPI) {
        setElectronAPI(window.electronAPI);
      }
    }
  }, []);

  return { electronAPI, api };
}
