'use client';

import React, { useMemo, useState, useCallback, useEffect } from 'react';
import { TypingStats } from './TypingStats';
import { TypingMonitor } from './TypingMonitor';
import { TypingHistory } from './TypingHistory';
import { TypingChart } from './TypingChart';
import { TabNavigation } from './TabNavigation';
import { MainLayout } from './MainLayout';
import { DebugPanel } from './DebugPanel';
import { Settings } from './Settings'; 
import RestartLoading from './RestartLoading';
// useToast    
// import { useToast } from './ToastContext';
import { useElectronApi } from '../hooks/useElectronApi';
import { useSettings } from '../hooks/useSettings';
import { useTypingStats } from '../hooks/useTypingStats';
import { useTabNavigation } from '../hooks/useTabNavigation';
import { useMemoryManagement } from '../hooks/useMemoryManagement';
import { useAutoHideHeader } from '../hooks/useAutoHideHeader';

// LogEntry   
interface LogEntry {
  id: number;
  content: string;
  key_count: number;
  typing_time: number;
  timestamp: string;
  created_at: string;
  is_saved?: boolean;
  window_title?: string;
  browser_name?: string;
  total_chars?: number;
  total_chars_no_space?: number;
  total_words?: number;
  pages?: number;
  accuracy?: number;
}

export const HomeContent = React.memo(function HomeContent() {
  // Electron API 
  const { electronAPI, api } = useElectronApi();
  
  //   
  const { 
    settings, 
    darkMode, 
    windowMode,
    handleSaveSettings, 
    handleDarkModeChange, 
    handleWindowModeChange 
  } = useSettings(electronAPI);
  
  //      
  const loadSettings = async () => {
    try {
      if (electronAPI) {
        const settings = await electronAPI.loadSettings();
        
        // GPU       
        const completeSettings: SettingsState = {
          ...settings,
          //    undefined     
          useHardwareAcceleration: settings.useHardwareAcceleration ?? false,
          processingMode: settings.processingMode ?? 'auto',
          maxMemoryThreshold: settings.maxMemoryThreshold ?? 100
        };
        
        // setSettings  handleSaveSettings 
        handleSaveSettings(completeSettings);
        // setDarkMode  handleDarkModeChange 
        handleDarkModeChange(settings.darkMode);
      }
    } catch (error) {
      console.error('   :', error);
    }
  };

  //  
  const { 
    activeTab, 
    debugMode, 
    handleTabChange, 
    toggleDebugMode 
  } = useTabNavigation({ 
    initialTab: 'monitor',
    electronAPI: api 
  });
  
  //    
  const { isHeaderVisible } = useAutoHideHeader({
    windowMode, 
    electronAPI
  });
  
  //    - 'typingLogs'    
  const { 
    logs: typingLogs, //  
    isLoading, 
    isTracking,
    displayStats,
    handleStartTracking,
    handleStopTracking,
    handleSaveStats,
    currentStatsRef,
    fetchLogs
  } = useTypingStats(electronAPI);
  
  //    
  const [currentLogs, setCurrentLogs] = useState<LogEntry[]>([]);
  
  //     
  const memoryManager = useMemoryManagement({
    debugMode,
    activeTab,
    memoryThreshold: 100, // 100MB 
    checkInterval: 30000, // 30 
    onClearLogs: () => {
      //        
      if (activeTab !== 'history' && activeTab !== 'stats' && activeTab !== 'chart') {
        setCurrentLogs([]);
        
        //       
        if (window.__memoryOptimizer?.optimizeImageResources) {
          window.__memoryOptimizer.optimizeImageResources().catch(err => {
            console.error('    :', err);
          });
        }
      }
    }
  });

  //       
  useEffect(() => {
    //   
    if (window.__memoryOptimizer?.optimizeMemory) {
      window.__memoryOptimizer.optimizeMemory(false);
    }
    
    //     (   )
    const imageOptimizationInterval = setInterval(() => {
      if (window.__memoryOptimizer?.optimizeImageResources) {
        window.__memoryOptimizer.optimizeImageResources().catch(err => {
          console.error('      :', err);
        });
      }
    }, 300000); // 5
    
    //      
    memoryManager.addInterval(imageOptimizationInterval);
    
    return () => {
      memoryManager.clearIntervals();
    };
  }, [memoryManager]);

  //    -     
  React.useEffect(() => {
    if (typingLogs && typingLogs.length > 0) {
      setCurrentLogs(typingLogs);
    }
  }, [typingLogs]);

  const [isRestarting, setIsRestarting] = useState(false);

  //    
  useEffect(() => {
    //     
    if (!electronAPI) return;
    
    const unsubscribe = electronAPI.onShowRestartLoading?.((data: RestartLoadingData) => {
      setIsRestarting(true);
      
      //    
      if (data.timeout) {
        setTimeout(() => {
          setIsRestarting(false);
        }, data.timeout);
      }
    }) || (() => {});
    
    return () => {
      unsubscribe();
    };
  }, [electronAPI]);

  //   
  const renderContent = useCallback(() => {
    switch (activeTab) {
      case 'monitor':
        return (
          <TypingMonitor
            stats={displayStats}
            isTracking={isTracking}
            onStartTracking={handleStartTracking}
            onStopTracking={handleStopTracking}
            onSaveStats={handleSaveStats}
          />
        );
      case 'stats':
        return (
          <React.Suspense fallback={<div>Loading chart...</div>}>
            <TypingChart 
              logs={typingLogs}
            />
          </React.Suspense>
        );
      case 'settings':
        return (
          <div className="settings-tab-wrapper" style={{
            maxWidth: '800px',
            margin: '0 auto',
            width: '100%'
          }}>
            <Settings
              onSave={handleSaveSettings}
              initialSettings={settings}
              darkMode={darkMode}
              onDarkModeChange={handleDarkModeChange}
              onWindowModeChange={handleWindowModeChange}
            />
          </div>
        );
      default:
        return null;
    }
  }, [
    activeTab, 
    displayStats, 
    isTracking, 
    handleStartTracking, 
    handleStopTracking, 
    handleSaveStats,
    typingLogs,
    settings, 
    darkMode, 
    handleDarkModeChange, 
    handleWindowModeChange,
    handleSaveSettings
  ]);

  return (
    <MainLayout 
      darkMode={darkMode}
      windowMode={windowMode} 
      electronAPI={electronAPI}
      isHeaderVisible={isHeaderVisible}
    >
      {/*    */}
      <RestartLoading 
        isVisible={isRestarting}
        message="  "
      />
      
      {/*   */}
      <TabNavigation
        activeTab={activeTab}
        onTabChange={handleTabChange}
        onDebugToggle={toggleDebugMode}
        debugMode={debugMode}
      />
      
      {/*    */}
      {renderContent()}
      
      {/*   */}
      <DebugPanel
        isVisible={debugMode}
        stats={currentStatsRef.current}
        logsCount={currentLogs.length}
        isTracking={isTracking}
        windowMode={windowMode}
      />
    </MainLayout>
  );
});

export default HomeContent;
